<template>
    <v-container>
        <v-row>
            <v-col cols="12">
                <h2>{{fillYourProfileText}}</h2>
            </v-col>
            <v-col cols="12">
                <v-card>
                    <v-progress-linear
                            value="25"
                            height="10"
                            color="blue-grey"
                            reactive
                            dark
                    >
                    </v-progress-linear>

                    <v-container>
                        <profile-info-selector
                                :show-username-field="true"
                                :birth-date-initial-value="birthDatePreview"
                                @dataValidityUpdate="isDataValid($event)">
                        </profile-info-selector>

                        <v-btn block color="primary" @click="setProfileInfo" :disabled="buttonIsDisabled">{{toContinueText}}</v-btn>
                    </v-container>
                </v-card>
            </v-col>

            <!-- Error snackbar -->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="blue"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
            </v-btn>
        </v-snackbar>
            <!--End snackbar-->
        </v-row>
    </v-container>
</template>

<script>

    import ProfileInfoSelector from "../AuxInputComponents/ProfileInfoSelector";
    import {mapActions} from 'vuex';
    import Constants from "../../store/Constants";
    const minimumAge = Constants.RULE_MIN_VALUE_AGE;
    
    export default {
        name: "fillYourProfileInfo",
        components: {ProfileInfoSelector},
        data() {
            return {
                genderText: Constants.TEXT_GENDER,
                toContinueText: Constants.TEXT_TO_CONTINUE,
                tellUsALittleAboutYouText: Constants.TEXT_TELL_US_A_LITTLE_ABOUT_YOU,
                birthDateText: Constants.TEXT_BIRTHDATE,
                fillYourProfileText: Constants.TEXT_FILL_YOUR_PROFILE,
                usernameText: Constants.TEXT_USERNAME,
                cancelText:Constants.TEXT_CANCEL,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                username:'',
                menu: false,
                modal: false,
                description: '',
                gender: null,
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods: {
            ...mapActions('user', ['createProfile']),
            isDataValid: function (data) {
                if (data.isValid) {
                    this.username = data.username;
                    this.birthDate = data.birthDate;
                    this.gender = data.gender;
                    this.description = data.description;

                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },
            setProfileInfo: function () {

                let resultCreateProfile = this.createProfile({
                    username: this.username,
                    birthDate: (new Date(this.birthDate)).setHours(0,0,0,),
                    gender: this.gender.id,
                    description: this.description
                });

                resultCreateProfile
                    .then(() => {
                        this.$router.push('fill-your-languages');
                    })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },

        },
        computed: {

            birthDatePreview: function () {
                let date = new Date();
                date.setFullYear(date.getFullYear()-minimumAge);
                return date;
            }
        },

    }
</script>

