<template>
    <v-form v-model="validForm">
        <v-text-field
                v-if="showUsernameField"
                v-model="username"
                :rules="[rules.required, rules.min4Char, rules.validUsername, rules.max14Char]"
                :label="usernameText"
                counter
                maxlength="14"
                required
                @input="formUpdated"
        ></v-text-field>

        <div v-if="!showUsernameField" style="margin-top: 10px"></div>
        <v-dialog
                ref="dialog"
                v-model="modal"
                :return-value.sync="birthDate"
                persistent
        >
            <template v-slot:activator="{ on }">
                <v-text-field
                        v-model="birthDateInTextMonthFormat"
                        :label="birthDateText"
                        append-icon="mdi-calendar"
                        :rules="[rules.required, rules.minAge]"
                        readonly
                        v-on="on"
                        required
                ></v-text-field>
            </template>
            <v-date-picker v-model="birthDate" scrollable width="97%" locale="es">
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="modal = false">{{cancelText}}</v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(birthDate)">OK</v-btn>
            </v-date-picker>
        </v-dialog>

        <v-select
                v-model="gender"
                :rules="[rules.required]"
                style="margin-top: 15px"
                :items="getGenderOptionsAsArray"
                item-value="id"
                item-text="option"
                :label="genderText"
                dense
                return-object
                @input="formUpdated"
        >

            <template v-slot:item="{item}">
                <v-avatar>
                    <img :src="item.img" style="height: 60%; width: auto"/>
                </v-avatar>
                {{ item.text }}
            </template>
            <template v-slot:selection="{item, select, selected }">
                <v-avatar>
                    <img :src="item.img" style="height: 50%; width: auto"/>
                </v-avatar>
                {{ item.text }}
            </template>
        </v-select>

        <v-textarea
                style="margin-top: 15px"
                outlined
                v-model="description"
                :rules="[rules.required, rules.min20Char]"
                :label="tellUsALittleAboutYouText"
                required
                counter
                maxlength="140"
                @input="formUpdated"
        ></v-textarea>
    </v-form>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapGetters} from "vuex";

    const minimumAge = Constants.RULE_MIN_VALUE_AGE;

    export default {
        name: "ProfileInfoSelector",
        props: {
            showUsernameField: Boolean,
            birthDateInitialValue: Date,
            genderInitialValue: String,
            descriptionInitialValue: String,
        },
        data() {
            return {
                genderText: Constants.TEXT_GENDER,
                toContinueText: Constants.TEXT_TO_CONTINUE,
                tellUsALittleAboutYouText: Constants.TEXT_TELL_US_A_LITTLE_ABOUT_YOU,
                birthDateText: Constants.TEXT_BIRTHDATE,
                fillYourProfileText: Constants.TEXT_FILL_YOUR_PROFILE,
                usernameText: Constants.TEXT_USERNAME,
                cancelText: Constants.TEXT_CANCEL,
                username: '',
                birthDate: null,
                birthDateInTextMonthFormat: null,
                firstTimeOpenCalendar: true,
                menu: false,
                modal: false,
                description: '',
                gender: null,
                validForm: false,
                rules: {
                    required: value => !!value || Constants.RULE_TEXT_MANDATORY_FIELD,
                    max14Char: value => (value.length <= Constants.RULE_MAX_VALUE_CHARACTERS_USERNAME) || Constants.RULE_TEXT_MAX_VALUE_CHARACTERS_USERNAME,
                    min4Char: value => (value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_USERNAME,
                    min20Char: value => (value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USER_DESCRIPTION || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_USER_DESCRIPTION,
                    minAge: value => {
                        return this.validBirthDate() || Constants.RULE_YOU_HAVE_TO_BE_AT_LEAST_X_YEARS_OLD_TO_REGISTER;
                    },
                    validUsername: value => {
                        const pattern1 = /[a-zA-Z_]+[a-zA-Z0-9_]*/
                        const pattern2 = /^_/
                        const pattern3 = /_[_]+/
                        return (pattern1.test(value) && !pattern2.test(value) && !pattern3.test(value)) || Constants.TEXT_USERNAME_NOT_VALID
                    },
                },
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods: {
            formUpdated: function () {
                let isValid = this.validForm;
                let data;

                if (isValid && this.validBirthDate()) {
                        data = {
                            isValid: true,
                            username: this.username,
                            birthDate:new Date(this.birthDate).setHours(0,0,0,),
                            gender: this.gender,
                            description: this.description
                        };

                } else {
                    data = {
                        isValid: false
                    };
                }

                this.$emit('dataValidityUpdate', data);
            },
            validBirthDate: function () {
                const bDate = new Date(this.birthDate);
                const currentDate = new Date();
                currentDate.setHours(0, 0, 0);
                let validAge;
                if (currentDate.getFullYear() - bDate.getFullYear() < minimumAge) {
                    validAge = false;
                } else if (currentDate.getFullYear() - bDate.getFullYear() == minimumAge) {
                    if (currentDate.getMonth() < bDate.getMonth()) {
                        validAge = false;
                    } else if (currentDate.getMonth() == bDate.getMonth()) {
                        if (currentDate.getDate() < bDate.getDate()) {
                            validAge = false;
                        } else {
                            validAge = true;
                        }
                    } else {
                        validAge = true;
                    }
                } else {
                    validAge = true;
                }
                return validAge;
            }
        },
        computed: {
            ...mapGetters('user', ['getGenderOptionsAsArray']),
        },
        watch: {
          birthDate: function(){
              this.birthDateInTextMonthFormat = this.$store.getters['user/getFormattedDateInTextMonth'](this.birthDate);
              this.formUpdated();
          }
        },
        mounted() {
            if (this.birthDateInitialValue) {
                this.birthDate = new Date(this.birthDateInitialValue).toISOString().substr(0, 10);
            }
            if (this.genderInitialValue) {
                this.gender = this.genderInitialValue;
            }

            if (this.descriptionInitialValue) {
                this.description = this.descriptionInitialValue;
            }
        },

    }
</script>

